import { RefObject, useEffect, useCallback } from "react"
import { Size } from "../../render/types/Size"
import { useResizeObserver } from "./useResizeObserver"
import { useStateRef } from "../../utils/useStateRef"

function reportSize(element: HTMLElement | null, size: Size, setSize: (size: Size) => void) {
    if (!element) {
        return
    }
    const children = [...element.children]
    let maxHeight = 0
    let maxWidth = 0

    children.forEach((child: HTMLElement) => {
        maxHeight = Math.max(maxHeight, child.scrollHeight + child.offsetTop)
        maxWidth = Math.max(maxWidth, child.scrollWidth + child.offsetLeft)
    })

    if (size.width !== maxWidth || size.height !== maxHeight) {
        setSize({
            width: maxWidth,
            height: maxHeight,
        })
    }
}

export function useChildrenSize(
    ref: RefObject<HTMLElement>,
    options: Partial<{ observe: boolean; skipHook: boolean; initial: Size }> = {}
): Size {
    const { observe = false, skipHook = false, initial = { width: 0, height: 0 } } = options

    // We use a stateful ref here so that we can memoize the onResize callback,
    // which depends on the current size. If we used pure state instead, we'd
    // have to recreate the callback on each size change, and re-install the
    // ResizeObserver with the new callback. And re-installing ResizeObserver on
    // size change causes "loop limit exceeded" errors.
    const [sizeRef, setSize] = useStateRef<Size>(initial)

    const onResize = useCallback(() => {
        reportSize(ref.current, sizeRef.current, setSize)
    }, [ref, sizeRef, setSize])

    useResizeObserver(ref, onResize, { skipHook: skipHook || !observe, observeChildren: true })

    useEffect(() => {
        if (skipHook || observe) {
            return
        }
        reportSize(ref.current, sizeRef.current, setSize)
    })

    return skipHook ? initial : sizeRef.current
}
