import { useRef, useState, useCallback } from "react"

/**
 * Creates a ref that triggers re-rerender when you change its `current` value.
 *
 * Returns a read-only ref, and a setter to change its `current` and trigger
 * re-render. Both are guaranteed to have stable identitity.
 */
export function useStateRef<T>(initial: T): [{ readonly current: T }, (value: T) => void] {
    const ref = useRef(initial)
    const [_, setState] = useState(initial)

    const setRefAndState = useCallback((value: T) => {
        ref.current = value
        setState(value)
    }, [ref, setState])

    return [ref, setRefAndState]
}
